/************ Skills Css ************/
.skill {
  padding: 0 0 50px 0;
  display: flex;
  justify-content: center;
  background: #06090e;
  width: 100%;
}
.skill_bx {
  max-width: 1200px;
  background: #151515;
  border-radius: 64px;
  text-align: center;
  padding: 20px 20px;
  margin-top: -60px;
}
.skill_bx_inside {
  padding: 15px;
  border-radius: 44px;
  border: 3px solid #00fffd;
}
.skill h2 {
  font-size: 45px;
  font-weight: 700;
}
.skill p {
  color: #b8b8b8;
  font-size: 18px;
  letter-spacing: 0.8px;
  line-height: 1.5em;
  margin: 14px 0;
}
.skill ul {
  color: #b8b8b8;
  font-size: 18px;
  display: flex;
  flex-direction: column;
  max-width: 700px;
  text-align: left;
  margin: auto;
}
.skill_slider {
  width: 80%;
  margin: 0 auto;
  position: relative;
}
.skill_slider .item img {
  width: 50%;
  margin: 0 auto 15px auto;
}
.background_image_left {
  top: 28%;
  position: absolute;
  bottom: 0;
  width: 40%;
  z-index: -4;
}

@media (max-width: 700px) {
  .skill p {
    font-size: 14px;
  }
  .skill ul {
    font-size: 14px;
    list-style-type: none;
  }
}
