/************ Banner Css ************/
.banner {
  display: flex;
  justify-content: center;
  align-items: center;
  max-height: 1000px;
  margin-top: 0;
  padding: 260px 0 100px 0;
  background-image: url('../assets/images/cyber-bg2.jpg');
  background-position: top center;
  background-size: cover;
  background-repeat: no-repeat;
  transition: 0.3s ease-in-out;
}

@media (max-width: 2000px) {
  .banner p {
    background-color: rgba(0, 0, 0, 0.8);
    padding: 10px;
    border-radius: 10px;
  }
}
.box_banner {
  margin-left: 20px;
  display: flex;
  flex-direction: column;
}
.banner .tagline {
  justify-content: center;
  font-weight: 700;
  letter-spacing: 0.8px;
  padding: 8px 10px;
  background: linear-gradient(90.21deg, #00e5ff -5.91%, rgba(74, 47, 189, 0.5) 111.58%);
  border: 1px solid rgba(255, 255, 255, 0.5);
  font-size: 20px;
  margin-bottom: 16px;
  display: inline-block;
}
.banner h1 {
  font-size: 65px;
  font-weight: 700;
  letter-spacing: 0.8px;
  line-height: 1;
  margin-bottom: 20px;
  display: block;
  max-width: 700px;
}
.banner p {
  color: #e1e0e0;
  font-size: 18px;
  letter-spacing: 0.8px;
  line-height: 1.5em;
  width: 96%;
  max-width: 700px;
}
.banner a {
  color: #e1e0e0;
  font-size: 18px;
  letter-spacing: 0.8px;
  line-height: 1.5em;
  width: 96%;
  max-width: 700px;
  transition: 0.3s ease-in-out;
}
.banner a:hover {
  color: #00e5ff;
  font-size: 18px;
  letter-spacing: 0.8px;
  line-height: 1.5em;
  width: 96%;
  max-width: 700px;
}
.banner img {
  width: 250px;
  height: 250px;
  padding: 5px;
  border: 3px solid #00e5ff;
  border-radius: 50%;
  animation: glitch-lines 5s infinite;
}

@keyframes glitch-lines {
  0% {
    transform: translateY(0);
  }
  20% {
    transform: translateY(-3px);
  }
  40% {
    transform: translateY(2px);
  }
  60% {
    transform: translateY(-2px);
  }
  80% {
    transform: translateY(3px);
  }
  100% {
    transform: translateY(0);
  }
}

.txt_rotate > .wrap {
  border-right: 0.08em solid #666;
}

.avatar {
  margin-right: 20px;
}

@media (max-width: 703px) {
  .banner {
    display: flex;
    background-image: url(../assets/images/cyber-bg4.jpg);
    font-size: 10px;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center;
  }
  .box_banner {
    justify-content: center;
    align-items: center;
  }
  .banner h1 {
    font-size: 25px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    max-width: 350px;
    background-color: rgba(0, 0, 0, 0.8);
    padding: 10px;
    border-radius: 10px;
  }

  .banner .tagline {
    font-size: 15px;
  }
  .banner p {
    font-size: 14px;
  }
  .banner a {
    font-size: 14px;
  }
  .banner img {
    width: 150px;
    height: 150px;
    padding: 5px;
    border: 3px solid #00e5ff;
    border-radius: 50%;
    animation: updown 3s linear infinite;
  }
  .avatar {
    align-items: center;
    margin-top: 10px;
    margin-right: 0;
  }
  .about {
    max-width: 350px;
  }
}

.card_wrapper {
  display: flex;
  justify-content: center;
  align-items: center;
}
